.blog-section {
    padding-top: 8rem;

    @media (max-width: 500px) {
        padding-top: 5rem;
    }
}

.blog-tags {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 1.3rem;
    margin: -0.7rem -0.7rem 0;

    > * {
        margin: 0.7rem;
    }
}

.blog-tag {
    @extend %vertical-align;
    font-weight: 500;
    padding: 0.8rem 1rem;
    border: 0.1rem solid var(--dark-gray);
    transition: $transition all;

    svg {
        width: 1.6rem;
        height: 1.6rem;
        margin-left: 0.5rem;
        transition: $transition fill;
    }

    @media (any-hover: hover) {
        &:hover {
            background-color: var(--dark-gray);
            color: #fff;

            svg {
                fill: #fff;
            }
        }
    }
}

.blog-grid {
    display: grid;
    grid-gap: var(--gap);

    @media (min-width: 768px) {
        grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 1024px) {
        grid-gap: 3rem;
    }

    &__item {

        &:nth-child(10n + 1),
        &:nth-child(10n + 7) {
            grid-row: span 2;
            grid-gap: 1.2rem;

            @media (min-width: 768px) and (max-width: 1024px) {
                grid-column: span 2;
            }

            @media (min-width: 1025px) {
                grid-template-columns: 1fr;
            }

            .blog-item__img {
                height: 33.6rem;

                @media (max-width: 767px) {
                    height: 22.3rem;
                }
            }
        }

        &:nth-child(10n + 7) {
            @media (max-width: 1024px) {
                grid-column: unset;
                grid-row: unset;
            }

            .blog-item__img {
                @media (max-width: 1024px) {
                    height: 22.3rem;
                }
            }
        }

        &:nth-child(10n + 6) {
            @media (min-width: 768px) and (max-width: 1024px) {
                grid-column: span 2;
            }

            .blog-item__img {
                @media (max-width: 1024px) {
                    height: 33.6rem;
                }

                @media (max-width: 767px) {
                    height: 22.3rem;
                }
            }
        }
    }
}

.tags {
    display: flex;
    flex-wrap: wrap;
    margin: -0.5rem;
}

.date,
.blog-item-text,
.tag {
    color: var(--gray);
}

.tag {
    @media (any-hover: hover) {
        &:hover {
            color: #000;
        }
    }
}

.date {
    display: block;
}

.blog-item-title {
    font-weight: 500;

    a {
        @media (any-hover: hover) {
            &:hover {
                color: var(--gray);
            }
        }
    }
}

.blog-item-text {
    flex-grow: 1;

    &:not(:last-child) {
        margin-bottom: 1.2rem;
    }
}

.blog-item {
    display: grid;
    align-content: flex-start;
    grid-gap: var(--gap);

    @media (max-width: 1024px) {
        grid-gap: 1.2rem;
    }

    @media (min-width: 1025px) {
        grid-template-columns: repeat(2, 1fr);
    }

    // .blog-item__img

    &__img {
        display: block;

        @media (max-width: 1024px) {
            height: 22.3rem;
        }

        img {
            @extend %cover;
            transition: 1.5s transform;
        }

        @media (any-hover: hover) {
            &:hover {
                img {
                    transform: scale(1.015);
                }
            }
        }
    }

    &__content {
        @extend %flex-col;

        > * {
            &:not(:last-child) {
                margin-bottom: 0.8rem;
            }
        }
    }

    // .blog-item__date

    &__date {
        @extend %text-13;
    }

    // .blog-item__title

    &__title {
        @extend %text-20;
    }

    // .blog-item__tag

    &__tag {
        @extend %text-13;
        margin: 0.5rem;
    }
}

.blog-page {
    padding-bottom: 13.8rem - var(--gap);

    &__title {
        &:not(:last-child) {
            margin-bottom: 3rem;
        }
    }

    .pagination {
        justify-content: center;
    }
}

.blog-text {
    &__tag {
        margin: 0.5rem;
    }

    // .blog-text__inner

    &__inner {
        @media (max-width: 1024px) {
            display: grid;
            grid-gap: var(--gap);
        }

        @media (min-width: 1025px) {
            display: flex;
            justify-content: space-between;
        }
    }

    &__content {
        @media (min-width: 1025px) {
            width: calc(983 / 1532 * 100%);
        }

        > * {
            &:not(:last-child) {
                margin-bottom: 4rem;
            }
        }

        ul,
        li {
            @extend %text-16;
            list-style-type: disc;
            margin-left: 1rem;
        }

        h3,
        h4,
        h5,
        h6 {
            @extend %text-17;
            font-weight: 600;
        }
    }

    &__aside {
        @media (min-width: 1025px) {
            width: 46.5rem;
            flex-shrink: 0;
        }
    }

    // .blog-text__tags

    &__tags {
        &:not(:last-child) {
            margin-bottom: 3rem;
        }
    }

    // .blog-text__text

    &__text {
        p {
            @extend %text-16;

            a {
                font-weight: 600;
            }

            &:not(:last-child) {
                margin-bottom: 2.5rem;

                @media (max-width: 500px) {
                    margin-bottom: 1.5rem;
                }
            }
        }
    }

    // .blog-text__slider

    &__slider {
        width: 100%;

        @media (max-width: 1024px) {
            width: calc(100vw - 1.6rem * 2);
        }

        .swiper-button-prev {
            left: 2rem;
        }

        .swiper-button-next {
            right: 2rem;
        }

        .swiper-slide {
            height: 63.7rem;

            @media (max-width: 1024px) {
                height: 50rem;
            }

            @media (max-width: 767px) {
                height: 35rem;
            }

            img {
                @extend %cover;
            }
        }
    }
}

.news-page {
    &__grid {
        display: grid;
        grid-gap: var(--gap);

        @media (max-width: 1024px) {
            grid-gap: 3rem;
        }

        @media (min-width: 768px) {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

.other-news {
    &__top {
        @extend %flex-row-center;

        &:not(:last-child) {
            margin-bottom: 4rem;
        }
    }

    .blog-item__img {
        min-height: 22.3rem;
    }
}
